// Define our labelmap
const labelMap = {
    1:{name:'person', color:'red'},
    2:{name:'cartellino', color:'yellow'},
    3:{name:'ThankYou', color:'lime'},
    4:{name:'LiveLong', color:'blue'},
}

// Define a drawing function
export const drawRect = (boxes, classes, scores, threshold, imgWidth, imgHeight, ctx, gg)=>{
    for(let i=0; i<=boxes.length; i++){
        if(boxes[i] && classes[i] && scores[i]>threshold){
            // Extract variables
            const [y,x,height,width] = boxes[i]
            var text = classes[i]
            if (text === 1 && !gg) {
                console.log("persona")
                // Set styling
                ctx.strokeStyle = labelMap[text]['color']
                ctx.lineWidth = 5
                ctx.fillStyle = 'white'
                ctx.font = '30px Arial'

                // DRAW!!
                ctx.beginPath()
                ctx.fillText(labelMap[text]['name'] + ' - ' + Math.round(scores[i] * 100) / 100, x * imgWidth, y * imgHeight - 10)
                ctx.rect(x * imgWidth, y * imgHeight, (width - x) * imgWidth, (height - y) * imgHeight);
                ctx.stroke()
            }
            else if (text === 1 && gg) {
                console.log("cartellino");
                console.log(boxes[i]);
                text = 2;
                ctx.strokeStyle = labelMap[text]['color']
                
                ctx.lineWidth = 2;
                    console.log("provaprovaprova");
                
                    
                
                ctx.fillStyle = 'white'
                ctx.font = '30px Arial'

                // DRAW!!
                console.log(ctx.lineWidth);
                ctx.beginPath()
                ctx.fillText(labelMap[text]['name'] + ' - ' + Math.round(scores[i] * 100) / 100, x * imgWidth, y * imgHeight - 10)
                ctx.rect(x * imgWidth, y * imgHeight, (width - x) * imgWidth, (height - y) * imgHeight);
                ctx.stroke()
                }
            
        }
    }
}